* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background: #b8e7f1;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
input {
  margin-left: 80px;
  margin-top: 40px;
}
.flag {
  width: 50px;
}
li {
  list-style: none;
}
.country {
  display: flex;
  align-items: center;
  margin-left: 38px;
}
.flag {
  margin-right: 30px;
}
.details__item {
  display: flex;
  align-items: center;
  font-weight: bold;
}
.details__text {
  font-weight: normal;
  margin: 0 0 0 20px;
}
